// src/api/axios.js
import axios from 'axios'
import { useLoadingStore } from '@/stores/loadingStore'
import { useUserStore } from '@/stores/userStore'
import router from '@/router'
import { $alert } from '@/utils/alertUtil'

/**
 * baseURL Í≤∞Ï†ï Í∑úÏπô (Vue CLI Ï†ÑÏö©)
 * 1) window.__APP_CONFIG__Í∞Ä ÏûàÏúºÎ©¥ ÏµúÏö∞ÏÑ† (assets/vue_app/app-config.json Îì±ÏóêÏÑú Ï£ºÏûÖ)
 * 2) file:// Î°ú Ïó¥Î†∏Îã§Î©¥(Flutter WebView Î°úÏª¨ ÏûêÏÇ∞) Ï†àÎåÄ URL ÏÇ¨Ïö©
 *    - env: VUE_APP_FILE_API_BASE, VUE_APP_API_CONTEXT
 *    - ÏóÜÏúºÎ©¥ Í∏∞Î≥∏Í∞í 'http://172.30.1.4:8080' + '/prafta'
 * 3) Í∑∏ Ïô∏(ÏõπÏÑúÎ≤ÑÏóêÏÑú ÏÑúÎπÑÏä§ Ï§ë)Îäî '/prafta' (devÎäî proxy, prodÎäî Î¶¨Î≤ÑÏä§ÌîÑÎ°ùÏãú Ï†ÑÏ†ú)
 */
function resolveBaseURL() {
  // 1) Ïô∏Î∂Ä ÏÑ§Ï†ï Ï£ºÏûÖ
  const cfg = (typeof window !== 'undefined' && window.__APP_CONFIG__) || {}
  if (cfg.API_BASE) {
    const context = cfg.CONTEXT ?? '/prafta'
    return `${cfg.API_BASE}${context}`
  }

  // 2) file:// ÌôòÍ≤Ω (Flutter WebView Í∞Ä assetsÎ°ú Ïó¨Îäî Í≤ΩÏö∞)
  if (typeof window !== 'undefined' && window.location?.protocol === 'file:') {
    const env = (typeof process !== 'undefined' && process.env) || {}
    const apiBase = env.VUE_APP_FILE_API_BASE || 'http://172.30.1.4:8080' // ‚Üê ÌïÑÏöî Ïãú https Î°ú Î≥ÄÍ≤Ω
    const context = env.VUE_APP_API_CONTEXT || '/prafta'
    return `${apiBase}${context}`
  }

  // 3) ÏõπÏÑúÎ≤ÑÎ°ú ÏÑúÎπÑÏä§ÎêòÎäî Í≤ΩÏö∞
  return '/prafta'
}

const api = axios.create({
  baseURL: resolveBaseURL(),
  timeout: 10000,
  headers: {
    'Content-Type': 'application/json',
  },
})

// ÏöîÏ≤≠ Ïù∏ÌÑ∞ÏÖâÌÑ∞
api.interceptors.request.use(
  async (config) => {
    const loadingStore = useLoadingStore()
    loadingStore.startLoading()

    const userInfo = {
      gv_cmpnyCd: sessionStorage.getItem('gv_cmpnyCd'),
      gv_userId: sessionStorage.getItem('gv_userId'),
      gv_userNm: sessionStorage.getItem('gv_userNm'),
      gv_authCd: sessionStorage.getItem('gv_authCd'),
      gv_mblNo: sessionStorage.getItem('gv_mblNo'),
      gv_email: sessionStorage.getItem('gv_email'),
    }

    const method = (config.method || 'get').toLowerCase()
    if (method === 'get') {
      config.params = { ...(config.params || {}), ...userInfo }
    } else if (!Array.isArray(config.data)) {
      config.data = { ...(config.data || {}), ...userInfo }
    }

    const token = sessionStorage.getItem('token')
    if (token) {
      config.headers = config.headers || {}
      config.headers.Authorization = `Bearer ${token}`
    }

    if (typeof window !== 'undefined' && window.location?.protocol === 'file:') {
      console.debug('[AXIOS file://] =>', config.method?.toUpperCase(), config.baseURL, config.url)
    }

    return config
  },
  (error) => {
    try {
      useLoadingStore().stopLoading()
    } catch {
      console.log(error)
    }
    return Promise.reject(error)
  }
)

// ÏùëÎãµ Ïù∏ÌÑ∞ÏÖâÌÑ∞
api.interceptors.response.use(
  (response) => {
    try {
      useLoadingStore().stopLoading()
    } catch {
      console.log(response)
    }
    return response
  },
  (error) => {
    try {
      useLoadingStore().stopLoading()
    } catch {
      console.log(error)
    }

    const userStore = useUserStore()
    const status = error?.response?.status

    // Í∏∞Ï°¥ ÏΩîÎìú Î≥¥Ï†ï: error.statusÍ∞Ä ÏïÑÎãàÎùº error.response.status
    if (status === 404 && error?.response?.data?.message === 'Ïú†Ìö®ÌïòÏßÄ ÏïäÏùÄ ÌÜ†ÌÅ∞ÏûÖÎãàÎã§.') {
      $alert(error.response.data.message)
      sessionStorage.clear()
      userStore.logout()
      router.push('/')
      return new Promise(() => {}) // Ï≤¥Ïù∏ Ï§ëÎã®
    }

    console.error(
      '[AXIOS][ERROR]',
      status,
      error?.message,
      error?.config?.method,
      error?.config?.url
    )
    return Promise.reject(error)
  }
)

export default api
