// src/router/index.js
import { createRouter, createWebHashHistory } from 'vue-router'
import { buildDynamicRoutes } from './dynamicRoutes'
import axios from '@/api/axios'

const routes = [
  { path: '/', name: 'Login', component: () => import('@/views/login/LoginView.vue') },
  { path: '/MainView', name: 'MainView', component: () => import('@/views/main/MainView.vue') },

  // Î°úÍ∑∏Ïù∏ Ï†Ñ ÎÖ∏Ï∂ú ÌôîÎ©¥Îì§ (ÌçºÎ∏îÎ¶≠)
  {
    path: '/ActInfoSrch',
    name: 'ActInfoSrch',
    component: () => import('@/views/login/ActInfoSrch.vue'),
  },
  { path: '/TermsInfo', name: 'TermsInfo', component: () => import('@/views/login/TermsInfo.vue') },
  {
    path: '/TermsDetail',
    name: 'TermsDetail',
    component: () => import('@/views/login/TermsDetail.vue'),
  },
  {
    path: '/QrScanner',
    name: 'QrScanner',
    component: () => import('@/views/_common/QrScanner.vue'),
  },
  {
    path: '/ChkLst',
    name: 'ChkLst',
    component: () => import('@/views/chkLst/ChkLst.vue'),
  },
  { path: '/JoinUser', name: 'JoinUser', component: () => import('@/views/login/JoinUser.vue') },

  // catch-all
  {
    path: '/:pathMatch(.*)*',
    name: 'NotFound',
    component: () => import('@/views/_common/NotFound.vue'),
  },
]

const router = createRouter({
  history: createWebHashHistory(),
  routes,
})

let dynamicInjected = false

/** ÌçºÎ∏îÎ¶≠ ÌóàÏö© Í≤ΩÎ°ú Î™©Î°ù (Ï†ïÌôï ÏùºÏπò ÎòêÎäî prefix ÌóàÏö© ÌïÑÏöî Ïãú startsWithÎ°ú Ï≤¥ÌÅ¨) */
const publicPaths = [
  '/',
  '/MainView',
  '/ActInfoSrch',
  '/TermsInfo',
  '/TermsDetail',
  'QrScanner',
  '/JoinUser',
]

/** DBÏóêÏÑú Î©îÎâ¥ Ï°∞Ìöå (ÏòàÏãú API) */
async function fetchAppRoutes() {
  try {
    const response = await axios.post('/comApi/baseinfo/getAppMenuList', { menuSrc: '002' })
    if (response.status === 200) return response.data || []
  } catch (err) {
    console.error('Î©îÎâ¥ Ï°∞Ìöå Ïã§Ìå®:', err)
  }
  return []
}

/** DB Í≤∞Í≥ºÎ•º ÎùºÏö∞Ìä∏Î°ú Î≥ÄÌôòÌïòÏó¨ Îì±Î°ù */
async function injectDynamicRoutes() {
  if (dynamicInjected) return
  const dbRoutes = await fetchAppRoutes()
  if (!dbRoutes || dbRoutes.length === 0) {
    dynamicInjected = true
    return
  }

  const dynamicRoutes = buildDynamicRoutes(dbRoutes)

  // ÎèôÏ†Å ÎùºÏö∞Ìä∏ Îì±Î°ù: Í∞úÎ∞ú Ï†ïÏ±ÖÏóê ÎßûÏ∂∞ Î∂ÑÎ•ò(Ïòà: login/* ÏùÄ Î£®Ìä∏Ïóê, main/* ÏùÄ MainView ÌïòÏúÑ) Îì±
  dynamicRoutes.forEach((r) => {
    // Í∏∞Î≥∏Ï†ÅÏúºÎ°ú ÎèôÏ†Å ÎùºÏö∞Ìä∏Îäî Ïù∏Ï¶ù ÌïÑÏöî(true) Î°ú ÌëúÍ∏∞
    if (!r.meta) r.meta = {}
    if (typeof r.meta.requiresAuth === 'undefined') r.meta.requiresAuth = true

    // Ïó¨Í∏∞ÏÑúÎäî route.pathÏù¥ '/...' Í∏∞Ï§ÄÏúºÎ°ú ÎßåÎì§Ïñ¥Ï°åÎã§Í≥† Í∞ÄÏ†ï
    // Ïòà: Î°úÍ∑∏Ïù∏ Ï†Ñ ÎÖ∏Ï∂ú(ÌäπÏ†ï Í≤ΩÎ°ú Ìå®ÌÑ¥)Ïù¥ ÏûàÎã§Î©¥ meta.requiresAuth = falseÎ°ú Î≥ÄÍ≤Ω Í∞ÄÎä•
    // ÏòàÏãú: if (r.viewPath?.startsWith('login/')) r.meta.requiresAuth = false

    // ÎùºÏö∞ÌÑ∞Ïóê Ï∂îÍ∞Ä (Ïù¥ ÌîÑÎ°úÏ†ùÌä∏Îäî Î£®Ìä∏ Î†àÎ≤®Ïóê Ï∂îÍ∞Ä)
    if (!router.hasRoute(r.name)) {
      router.addRoute(r) // ÏµúÏÉÅÏúÑÎ°ú Îì±Î°ù
      console.log('ÎèôÏ†ÅÎùºÏö∞Ìä∏ Ï∂îÍ∞Ä:', r.path, r.name, 'requiresAuth:', r.meta.requiresAuth)
    }
  })

  dynamicInjected = true
}

/** beforeEach Í∞ÄÎìú */
router.beforeEach(async (to, from, next) => {
  // 1) ÎèôÏ†Å ÎùºÏö∞Ìä∏ Ï£ºÏûÖ Î≥¥Ïû•
  if (!dynamicInjected) {
    await injectDynamicRoutes()
  }

  // 2) ÌçºÎ∏îÎ¶≠ Í≤ΩÎ°úÏù∏ÏßÄ Ïö∞ÏÑ† Ï≤¥ÌÅ¨ (Ï†ïÌôï ÏùºÏπò ÎòêÎäî prefix Ï≤¥ÌÅ¨ ÌïÑÏöî Ïãú Î≥ÄÍ≤Ω)
  const isPublic = publicPaths.some((p) => {
    // Ï†ïÌôï ÏùºÏπò ÎòêÎäî pÍ∞Ä prefixÏù∏ Í≤ΩÏö∞ ÌóàÏö© (Ïòà: '/MainView' Îäî '/MainView/...' ÎèÑ ÌóàÏö©)
    if (p === to.path) return true
    if (to.path.startsWith(p + '/')) return true
    return false
  })

  if (isPublic) {
    // ÌçºÎ∏îÎ¶≠ÏùÄ Î°úÍ∑∏Ïù∏ Ïó¨Î∂ÄÏôÄ Î¨¥Í¥ÄÌïòÍ≤å Ï†ëÍ∑º ÌóàÏö©
    return next()
  }

  // 3) ÌçºÎ∏îÎ¶≠Ïù¥ ÏïÑÎãå Í≤ΩÏö∞: ÌÜ†ÌÅ∞ ÌôïÏù∏
  const token = sessionStorage.getItem('token')
  if (!token) {
    // Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄÎ°ú Î¶¨ÎîîÎ†âÌä∏ÌïòÎêò, ÏõêÎûò Í∞ÄÎ†§Îçò Í≤ΩÎ°úÎ•º ÏøºÎ¶¨Î°ú Î≥¥Ï°¥Ìï¥ÏÑú Î°úÍ∑∏Ïù∏ ÌõÑ Ïù¥Îèô Í∞ÄÎä•ÌïòÍ≤å Ìï®
    return next({ path: '/', query: { redirect: to.fullPath } })
  }

  // 4) ÎùºÏö∞Ìä∏Ïùò meta.requiresAuth Í≤ÄÏÇ¨(ÎèôÏ†Å ÎùºÏö∞Ìä∏Ïóê Îî∞Îùº ÏÑ∏Î∞ÄÌûà Î∂ÑÍ∏∞ Í∞ÄÎä•)
  if (to.meta?.requiresAuth === false) {
    return next()
  }

  // 5) Í∑∏ Ïô∏ Ï†ïÏÉÅ Ï†ëÍ∑º ÌóàÏö©
  return next()
})

export default router
